import React from 'react';
import { Helmet as ReactHelmet } from 'react-helmet';
import { useStaticQuery, graphql } from 'gatsby';

type propTypes = {
  title?: string;
};

const Helmet: React.FC<propTypes> = ({ title }) => {
  const { site } = useStaticQuery(
    graphql`
      query {
        site {
          siteMetadata {
            title
            titleTemplate
            description
            author
            siteUrl
          }
        }
      }
    `,
  );
  React.useEffect(() => {
    console.log('사이트', site);
  });
  return (
    <ReactHelmet
      htmlAttributes="ko-KR"
      title={title ? `${site.titleTemplate}${title}` : site.title}
    ></ReactHelmet>
  );
};

export default Helmet;
